:root {
    --tan: #FFFEE0;
    --red: #BE1010;
    --white: white;
    --black: #000000;
    --grey: #616161;
}
* {
    margin: 0
}
body {
    font-family: 'Poppins', sans-serif;
    background-color: var(--tan);
    overflow-y: scroll
}
/* nav */
.nav-container {
    background-color: var(--red);
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.nav-header {
    font-weight: 400;
    font-size: 22px;
    line-height: 48px;
    color: var(--white);
    text-decoration: none;
    margin: 0 60px;
}


/* gallery */
.gall-container {
    padding: 40px;
}
.gall-card-container {
    border-radius: 14px;
    border: 4px solid var(--black);
    background-color: var(--white);
    padding: 30px 60px;
    margin-bottom: 32px; 
}
.gall-card-front {
    font-weight: 400;
    font-size: 34px;
    line-height: 51px;
    margin: 0
}
.gall-card-break {
    margin: 24px 0;
    color: var(--grey)
}
.gall-card-back {
    font-weight: 400;
    font-size: 28px;
    line-height: 42px;
    color: var(--grey);
}

